.i {
    display: flex;
    height: 65vh;
}

.i-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.i-right {
    flex: 1;
    position: relative;
}

.i-left-wrapper {
    margin-top: 40px;
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro {
    font-size: 30px;
    font-weight: 300;
}

.i-name {
    font-size: 60px;
    font-family: 'Permanent Marker', cursive;
}

.i-title {
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    25%{
        transform: translateY(-50px);
    }
    50%{
        transform: translateY(-100px);
    }
    75%{
        transform: translateY(-150px);
    }
    100%{
        transform: translateY(-200px);
    }
}

.i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #003d7a;
    display: flex;
    align-items: center;
    font-family: 'Caveat', cursive;
    font-weight: 700;
    font-size: 40px;
}

.i-scroll {
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}

.i-desc {
    font-size: 30px;
    font-weight: 300;
}

.i-img {
    width: 100%;
    height: 80vh;
    object-fit: cover;
    position: relative;
}


@media screen and (max-width: 480px) {
    .i {
        flex-direction: column;
        height: 80vh;
    }

    .i-left-wrapper {
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .i-desc {
        display: none;
    }

    .i-name {
        left: 20px;
    }

    .i-title-item {
        align-items: center;
        text-align: center;
        width: 300px;
        justify-content: center;
    }

    .i-right {
        justify-content: center;
        align-items: center;
        display: flex;
    }

    .i-img {
        justify-content: center;
        align-items: center;
        display: flex;
        left: 10px;
        max-width: 80vw;
        height: 50vh;
        object-fit: cover;
    }

    .i-name {
        display: none;
    }
}